



























	export default {
    data() {
        return {
            defaultIndex:0,
			menuList:[
				{
					name:'新闻'
				},
				{
					name:'公告'
				},
				{
					name:'社会'
				}
			]
        }
    },
    methods: {
        tapTab:function(index){
			if (this.defaultIndex === index) {
				return false;
			}else{
				this.defaultIndex = index;
			}
		},
		
		swiperTab:function(e){
			if(e.detail.current === this.defaultIndex){
				return false;
			}else{
				this.defaultIndex = e.detail.current;
			}
		}
    }
}

